import request from "@/utils/request";

/**
 * 
 *  :  ,  id, 
 * - uid :  id
 * @param {number} uid
 */
export function userDetail(uid) {
  return request({
    url: "/user/detail",
    method: "get",
    params: {
      uid,
    },
  });
}

/**
 * 
 *  :  ,  id, 
 * - uid :  id
 * - limit :  ,  30
 * - offset :  ,  :(  -1)*30,  30  limit  ,  0
 * @param {Object} params
 * @param {number} params.uid
 * @param {number} params.limit
 * @param {number=} params.offset
 */
export function userPlaylist(params) {
  return request({
    url: "/user/playlist",
    method: "get",
    params,
  });
}

/**
 * 
 *  :  ,  id, id(id)
 * - uid:  id
 * @param {number} uid
 */
export function userLikedSongsIDs(uid) {
  return request({
    url: "/likelist",
    method: "get",
    params: {
      uid,
      timestamp: new Date().getTime(),
    },
  });
}

/**
 * 
 *  : 
 * -  type:  ,  0,  0  ,1  web/PC 
 * @param {number} type
 */
export function dailySignin(type = 0) {
  return request({
    url: "/daily_signin",
    method: "post",
    params: {
      type,
      timestamp: new Date().getTime(),
    },
  });
}

