import request from "@/utils/request";
import { mapTrackPlayableStatus } from "@/utils/common";

/**
 * 
 *  :  ,  id, 
 * @param {number} id
 */
export function getAlbum(id) {
  return request({
    url: "/album",
    method: "get",
    params: {
      id,
    },
  }).then((data) => {
    data.songs = mapTrackPlayableStatus(data.songs);
    return data;
  });
}

/**
 * 
 *  :  ,
 * - limit -  ,  30
 * - offset -  ,  :(  -1)*30,  30  limit  ,  0
 * - area - ALL:,ZH:,EA:,KR:,JP:
 * @param {Object} params
 * @param {number} params.limit
 * @param {number=} params.offset
 * @param {string} params.area
 */
export function newAlbums(params) {
  return request({
    url: "/album/new",
    method: "get",
    params,
  });
}

/**
 * 
 *  :  ,  id, ,,,,
 * - id - id
 * @param {number} id
 */
export function albumDynamicDetail(id) {
  return request({
    url: "/album/detail/dynamic",
    method: "get",
    params: { id, timestamp: new Date().getTime() },
  });
}

/**
 * /
 *  : ,/
 * - id -  id
 * - t - 1 ,
 * @param {Object} params
 * @param {number} params.id
 * @param {number} params.t
 */
export function likeAAlbum(params) {
  return request({
    url: "/album/sub",
    method: "post",
    params,
  });
}
