import request from "@/utils/request";
import { mapTrackPlayableStatus } from "@/utils/common";

/**
 * 
 *  :  ,  id, 
 * @param {number} id -  id, 
 */
export function getArtist(id) {
  return request({
    url: "/artists",
    method: "get",
    params: {
      id,
      timestamp: new Date().getTime(),
    },
  }).then((data) => {
    data.hotSongs = mapTrackPlayableStatus(data.hotSongs);
    return data;
  });
}

